<template>
    <div class="home commpage">
        <div class="common-flex">
            <div class="flex-big">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span class="common-pfc">电量总览 <i class="small-size">(kW·h）</i> </span>
                    </div>
                    <div class="home-card-box">
                        <div class="card-item">
                            <div class="light-color small-size time-text">{{today}}</div>
                            <div class="card-group">
                                <svg-icon icon-class="battery1" class-name="battery"></svg-icon>
                                <div>
                                    <div class="black-color big-size common-pfc" :title="historyCount.today+' kW·h'">
                                        {{historyCount.today?historyCount.today:'-' | changeUnit}}
                                        <span class="mid-size">
                                            {{historyCount.today>= 1000000 && historyCount.today < 100000000?'万':historyCount.today  > 100000000?'亿':''}}kW·h</span>
                                    </div>
                                    <div class="light-color mid-size">当日电量</div>
                                </div>
                            </div>
                        </div>
                        <div class="card-item">
                            <div class="light-color small-size time-text">{{yesterday}}</div>
                            <div class="card-group">
                                <svg-icon icon-class="battery2" class-name="battery"></svg-icon>
                                <div>
                                    <div class="black-color big-size common-pfc"
                                        :title="historyCount.yesterday+' kW·h'">
                                        {{historyCount.yesterday?historyCount.yesterday:'-'| changeUnit}}

                                        <span class="mid-size">
                                            {{historyCount.yesterday>= 1000000 && historyCount.yesterday < 100000000?'万':historyCount.yesterday  > 100000000?'亿':''}}kW·h
                                        </span>

                                    </div>
                                    <div class="light-color mid-size">昨日电量</div>
                                </div>
                            </div>
                        </div>
                        <div class="card-item nobottom">
                            <div class="light-color small-size time-text">{{tomonth}}</div>
                            <div class="card-group">
                                <svg-icon icon-class="battery3" class-name="battery"></svg-icon>
                                <div>
                                    <div class="black-color big-size common-pfc" :title="historyCount.month+' kW·h'">
                                        {{historyCount.month?historyCount.month:'-'| changeUnit}}
                                        <span class="mid-size">
                                            {{historyCount.month>= 1000000 && historyCount.month < 100000000?'万':historyCount.month  > 100000000?'亿':''}}kW·h
                                        </span>
                                    </div>
                                    <div class="light-color mid-size">当月电量</div>
                                </div>
                            </div>
                        </div>
                        <div class="card-item nobottom">
                            <div class="light-color small-size time-text">{{ toyear==='-'? '-':(toyear+'-'+total_end) }}
                            </div>
                            <div class="card-group">
                                <svg-icon icon-class="battery4" class-name="battery"></svg-icon>
                                <div>
                                    <div class="black-color big-size common-pfc" :title="historyCount.total+' kW·h'">
                                        {{historyCount.total?historyCount.total:'-'| changeUnit}}
                                        <span class="mid-size">
                                            {{historyCount.total>= 1000000 && historyCount.total < 100000000?'万':historyCount.total  > 100000000?'亿':''}}kW·h
                                        </span>
                                    </div>
                                    <div class="light-color mid-size">累计电量</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-card>
            </div>
            <div class="flex-small">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span class="common-pfc">实时监测</span>
                        <div class="monitor-select">
                            <el-select v-model="power" :popper-append-to-body="false" @change="handPower">
                                <el-option v-for="item in powerOptions" :label="item.label" :key="item.label"
                                    :value="item.value"></el-option>
                            </el-select>
                        </div>
                    </div>
                    <div class="home-card-box">
                        <div class="uab-box">
                            <div class="uab-item" v-for="item in powerData[power]" :key="item.key">
                                <div class="light-color uab-item-title">
                                    <img :src="item.svgName" class="monitor" alt="">
                                    <!-- <svg-icon :icon-class="item.svgName" class-name="monitor"></svg-icon> -->
                                    <span>{{item.maxText}}</span><sub
                                        style="vertical-align:sub; font-size:9px; font-family:Arial, Helvetica, sans-serif;margin:4px 0px 0px 0px">{{item.minText}}</sub>
                                </div>
                                <div class="black-color big-size common-pfc uab-item-cont">{{item.value | toFixed_2}}
                                    <span class="small-size"> {{item.unit}}</span></div>
                            </div>

                        </div>
                    </div>
                </el-card>
            </div>
            <div class="flex-small">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span class="common-pfc">负荷监测</span>
                    </div>
                    <div class="home-card-box">
                        <div class="load-box">
                            <div class="load-item">
                                <div class="light-color mid-size">
                                    <svg-icon icon-class="load1" class-name="loadSvg"></svg-icon>实时负荷
                                </div>
                                <div class="black-color big-size common-pfc">{{ssjc.p | toFixed_1}}
                                    <span class="mid-size"> kW</span>
                                </div>
                            </div>
                            <div class="load-item">
                                <div class="light-color mid-size">
                                    <svg-icon icon-class="load2" class-name="loadSvg"></svg-icon>实时负载率
                                </div>
                                <div class="black-color big-size common-pfc">{{ssjc.fzRate | toFixed_1}}
                                    <span class="mid-size"> %</span>
                                </div>
                            </div>
                            <div class="load-item">
                                <div class="light-color mid-size">
                                    <svg-icon icon-class="load3" class-name="loadSvg"></svg-icon>功率因数
                                </div>
                                <div class="black-color big-size common-pfc">{{ssjc.cos | toFixed_2}}</div>
                            </div>
                            <div class="load-item">
                                <div class="light-color mid-size">
                                    <svg-icon icon-class="load4" class-name="loadSvg"></svg-icon>历史最大负荷
                                </div>
                                <div class="black-color big-size common-pfc">{{ssjc.maxFh?parseInt(ssjc.maxFh):'-' }}
                                    <span class="mid-size">kW</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-card>
            </div>
        </div>
        <div class="common-flex">
            <div class="flex-big">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span class="common-pfc">电量趋势</span>
                        <div class="peak-select">
                            <el-select v-model="times" :popper-append-to-body="false" @change="handTime">
                                <el-option v-for="item in days" :label="item.label" :key="item.label"
                                    :value="item.value"></el-option>
                            </el-select>
                        </div>
                    </div>
                    <div class="peak-box card-bot">
                        <LineChart :class-name="'trendChart'" :char-type="'bar'" :chart-data="trendCharData" />
                        <div class="trendTag">
                            <span>目标均值</span>
                            <span>实际均值</span>
                        </div>
                    </div>
                </el-card>
            </div>
            <div class="flex-max">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span class="common-pfc">当日有功负荷趋势</span>
                    </div>

                    <div class="card-bot">
                        <LineChart :class-name="'power'" :char-type="'line'" :chart-data="powerCharData" />
                    </div>
                </el-card>
            </div>
        </div>

    </div>
</template>

<script>
    import {
        mapState
    } from 'vuex'
    import LineChart from '@/components/char'
    import {
        getHome,
        getTrend
    } from '@/api/home'
    import moment from 'moment'
    import tool from "@/utils/tool.js"
    export default {
        name: 'index',
        components: {
            LineChart
        },
        data() {
            return {
                times: 'thisDay',
                days: [{
                    label: '日',
                    value: 'thisDay',
                }, {
                    label: '月',
                    value: 'thisMonth',
                }],
                power: 'VObjs',
                powerOptions: [{
                    label: '高压/低压侧相电压',
                    value: 'VObjs',
                }, {
                    label: '高压/低压侧相电流',
                    value: 'IObjs',
                }],
                powerData: {
                    VObjs: [{
                        key: 'uA',
                        maxText: 'U',
                        minText: 'A',
                        unit: ' kV',
                        svgName: require('@/assets/img/5.png'),
                        value: ''
                    }, {
                        key: 'uB',
                        maxText: 'U',
                        minText: 'B',
                        unit: ' kV',
                        svgName: require('@/assets/img/6.png'),
                        value: ''
                    }, {
                        key: 'uC',
                        maxText: 'U',
                        minText: 'C',
                        unit: ' kV',
                        svgName: require('@/assets/img/7.png'),
                        value: ''
                    }], //电压
                    IObjs: [{
                        key: 'lab',
                        maxText: 'I',
                        minText: 'A',
                        unit: 'A',
                        svgName: require('@/assets/img/5.png'),
                        value: ''
                    }, {
                        key: 'lbc',
                        maxText: 'I',
                        minText: 'B',
                        unit: 'A',
                        svgName: require('@/assets/img/6.png'),
                        value: ''
                    }, {
                        key: 'lac',
                        maxText: 'I',
                        minText: 'C',
                        unit: 'A',
                        svgName: require('@/assets/img/7.png'),
                        value: ''
                    }], //电压//电流
                    temObj: [], //箱变温度
                },
                historyCount: {},
                work: null,
                todayCount: [],
                ssjc: {},
                lines: [{
                    key: 'pMax',
                    name: '有功负荷'
                }, {
                    key: 'qMax',
                    name: '无功负荷'
                }],
                powerCharData: {
                    legend: [],
                    xAxis: [],
                    series: [],
                    colors: ['#2E4AF7', '#FF8C56'],
                    name: ['kW/kvar'],
                },
                trendCharData: {
                    legend: [],
                    name: ['kW·h'],
                    formatter: "用电量 <br/>{b} : {c} ",
                    dataRoomShow: false,
                    series: [],
                    yAxis: [],
                    xAxis: [],
                    colors: ['#6D81FA'],
                    mbAv: null, //目标均值
                    sjAv: null //实际均值
                },
                today: '-',
                yesterday: '-',
                tomonth: '-',
                toyear: '-',
                total_end: '-'
            }
        },
        watch: {
            jp_code: {
                deep: true,
                handler(val) {
                    this.getTrend()
                }
            }
        },
        created() {
            this.today = moment(new Date()).format('YYYY/MM/DD')
            this.yesterday = moment().subtract(1, "days").format("YYYY/MM/DD");
            this.getHome();
            this.getTrend()
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId,
                jp_code: state => state.user.jp_code,
            })
        },
        mounted() {

        },
        methods: {
            //获取用户信息 
            getHome() {
                getHome(this.userId).then(res => {
                    const {
                        historyCount,
                        work,
                        ssjc,
                        todayCount
                    } = res
                    this.historyCount = historyCount;
                    this.tomonth = moment(historyCount.month_start).format('YYYY/MM/DD') + '-' + moment(
                        historyCount.month_end).format('YYYY/MM/DD');

                    this.toyear = moment(historyCount.total_start).format('YYYY/MM/DD')
                    this.total_end = moment(historyCount.total_end).format('YYYY/MM/DD')
                    this.work = work;
                    this.ssjc = ssjc;
                    this.todayCount = todayCount;
                    this.powerData.VObjs[0].value = ssjc.jlTy == 1 ? ssjc.uA / 1000 : ssjc.uA
                    this.powerData.VObjs[1].value = ssjc.jlTy == 1 ? ssjc.uB / 1000 : ssjc.uB
                    this.powerData.VObjs[2].value = ssjc.jlTy == 1 ? ssjc.uC / 1000 : ssjc.uC
                    this.powerData.VObjs[0].unit = ssjc.jlTy == 1 ? 'kV' : 'V'
                    this.powerData.VObjs[1].unit = ssjc.jlTy == 1 ? 'kV' : 'V'
                    this.powerData.VObjs[2].unit = ssjc.jlTy == 1 ? 'kV' : 'V'

                    this.powerData.IObjs[0].value = ssjc.iA
                    this.powerData.IObjs[1].value = ssjc.iB
                    this.powerData.IObjs[2].value = ssjc.iC
                    this.initOptions(todayCount)
                })
            },
            getTrend() {
                let query = {
                    jpcode: this.jp_code,
                    type: this.times
                }
                this.trendCharData.xAxis = [];
                this.trendCharData.yAxis = [];
                this.trendCharData.mbAv = 0;
                this.trendCharData.sjAv = 0;
                this.trendCharData.series = []
                this.trendCharData.name[0] = 'kW·h'
                if (query.jpcode != '') {
                    getTrend(query).then(res => {
                        this.trendCharData.mbAv = res.mbAv;
                        this.trendCharData.sjAv = res.sjAv;
                        this.trendCharData.grid = {
                            right: 24,
                            bottom: 20,
                            left: 24,
                            top: 68,
                            containLabel: true,
                        }
                        let w = res.list.some(item => {
                            return item.ePtp >= 1000000 && item.ePtp < 100000000
                        })
                        let y = res.list.some(item => {
                            return item.ePtp >= 100000000
                        })
                        res.list.map((item, index) => {
                            let time = moment(item.tsFirst).format(this.times === 'thisDay' ? 'H' :
                                'M/D')
                            this.trendCharData.xAxis.push(time)
                            let value = item.ePtp;
                            if (y) {
                                value = (item.ePtp / 100000000).toFixed(2)
                                if (index === 0) {
                                    this.trendCharData.mbAv = (res.mbAv / 100000000).toFixed(2)
                                    this.trendCharData.sjAv = (res.mbAv / 100000000).toFixed(2)
                                }
                                this.trendCharData.name[0] = '亿 kW·h'
                            } else if (w) {
                                value = (item.ePtp / 10000).toFixed(2)
                                if (index === 0) {
                                    this.trendCharData.mbAv = (res.mbAv / 10000).toFixed(2)
                                    this.trendCharData.sjAv = (res.mbAv / 10000).toFixed(2)
                                }
                                this.trendCharData.name[0] = '万 kW·h'
                            }

                            this.trendCharData.yAxis.push(value)
                        })

                        this.trendCharData.series.push({
                            type: 'bar',
                            smooth: true,
                            data: this.trendCharData.yAxis,
                            barWidth: 12,
                            markLine: {
                                symbol: "none", //去掉警戒线最后面的箭头
                                label: {
                                    position: "middle" //将警示值放在哪个位置，三个值“start”,"middle","end"  开始  中点 结束
                                },
                                data: [{
                                        type: "average",
                                        name: "目标均值",
                                        label: "目标均值",
                                        yAxis: this.trendCharData.mbAv,
                                        lineStyle: {
                                            color: '#FF8C56',
                                            type: "dashed"
                                        }
                                    },
                                    {
                                        label: "实际均值",
                                        name: "实际均值",
                                        yAxis: this.trendCharData.sjAv,
                                        lineStyle: {
                                            color: "#EF4474",
                                            type: "dashed"
                                        }
                                    }
                                ]
                            }
                        })
                    })
                }

            },
            handTime() {
                this.getTrend()
            },
            handPower() {
                if (this.power === 1) this.powerData = this.VObjs
                else if (this.power === 2) this.powerData = this.IObjs
            },
            initOptions(data) {
                let obj = {};
                this.lines.map(item => {
                    this.powerCharData.legend.push(item.name)
                    obj[item.key] = [];
                })
                data ? data.map(item => {
                    this.powerCharData.xAxis.push([item.showHourSt])
                    this.lines.map(it => {
                        obj[it.key].push(item[it.key])
                    })
                }) : []
                this.powerCharData.grid = {
                    right: 24,
                    bottom: 20,
                    left: 24,
                    top: 68,
                    containLabel: true,
                }
                this.lines.map(item => {
                    this.powerCharData.series.push({
                        name: item.name,
                        symbol: "none",
                        type: 'line',
                        itemStyle: {
                            normal: {
                                lineStyle: {
                                    width: 3 //设置线条粗细
                                }
                            }
                        },
                        smooth: true,
                        data: obj[item.key],
                    })
                })
                // this.initEcharts();
            },
            // initEcharts () {
            //   // this.myChart = echarts.init(document.getElementById('power'));

            //   this.options = {
            //     color: ['#2E4AF7', '#FF8C56'],
            //     tooltip: {
            //       trigger: 'axis'
            //     },
            //     legend: {
            //       data: this.powerCharData.legend,
            //       itemWidth: 10,
            //       icon: 'circle',
            //       right: 20,
            //       top: 0
            //     },
            //     grid: {
            //       right: 0,
            //       top: 30,
            //       bottom: 40,

            //     },
            //     dataZoom: [
            //       {
            //         show: true,
            //         realtime: true,
            //         start: 0,
            //         end: 100,
            //         xAxisIndex: [0, 1]
            //       },
            //       {
            //         type: 'inside',
            //         realtime: true,
            //         start: 0,
            //         end: 100,
            //         xAxisIndex: [0, 1]
            //       }
            //     ],
            //     xAxis: {
            //       type: 'category',
            //       data: this.powerCharData.xAxis
            //     },
            //     yAxis: {
            //       name: 'kW',
            //       nameTextStyle: {
            //         padding: [0, 0, 0, -32]    // 四个数字分别为上右下左与原位置距离
            //       },
            //       type: 'value',
            //     },
            //     series: this.powerCharData.series
            //   };
            //   this.myChart.setOption(this.options);
            // }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    @import "./common.scss";

    .time-text {
        font-weight: 500;
        color: #666666;
    }

    .dashed-box {
        .load-item {
            svg {
                border: 1px dashed #666;
            }
        }
    }

    .light-color {
        font-family: 'PFC'
    }

    .peak-box {
        position: relative;
    }
</style>